import {
	mapMutations,
	mapGetters
} from 'vuex';
export default {
	data() {
		return {
			num:1,
			dels: this.Static+'/my/ti.png',
			shu:[],
			numberss:false,
		}
	},
	components: {
		
	},
	watch: {
		GET_NEWS: {
			handler(a, b) {
				console.log(a, b)
				this.shu = a.num;
				if(this.num == 1 && this.shu.allCount != 0&& this.shu.allCount !=undefined){
					setTimeout(() => {
						this.numberss=true;
					}, 500);
				}
				console.log(this.shu)
			},
			deep: true
		},
	
	},
	
	computed: {
		i18n () {
		  return this.$t('News')  
		},
		...mapGetters(['GET_NEWS'])
	},
	methods: {
		del(){
			this.num = 0;
		},
	}
	
}
